@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;


    &.mostrar {
        padding: 2rem;
        opacity: 1;
        height: 150%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgb(0 0 0 / 95%);

        @include m.tablet {
            display: none;
        }
    }

    &.ocultar {
        opacity: 0;
    }

    @include m.tablet {
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$indigo;
    }

    h2 {
        display: flex;
        color: v.$blanco;
        margin: 0;
        text-align: center;
    }

    .sidebar-nav {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 5rem;

        @include m.tablet {
            display: block;   
        }

        a {
            color: v.$blanco;
            display: block;
            padding: .7rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-size: 2.4rem;
            margin-bottom: 2rem;

            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }

            @include m.tablet {
                font-size: 1.5rem;
                margin-bottom: 1.5rem;
            }
        }
    }

    .cerrar-sesion-mobile {
        margin-top: 10rem;

        .cerrar-sesion {
            @include m.boton(v.$indigo, v.$blanco);
        }

        @include m.tablet {
            display: none;
        }
    }

}