@use 'variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
.body {
    font-family: v.$fuente_textos;
    font-size: 1.6rem;
    min-height: 100%;

    &::-webkit-scrollbar {
        width: .9rem;
    }

    &::-webkit-scrollbar-track {
        background: v.$grisClaro;
    }

    &::-webkit-scrollbar-thumb {
        background-color: v.$grisOscuro;
        border-radius: 1rem;
    }
}
.overflow-hidden {
    overflow: hidden;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.contenedor-sm {
    max-width: 70rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
