@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin-top: 5rem;
    
    .campo {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        
        label {
            flex: 0 0 10rem;
        }

        input {
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: lighten(v.$gris, 40%);

            &:hover {
                transition: ease-in-out 0.1s;
                border-left: .5rem solid v.$grisOscuro;
                background-color: lighten(v.$grisOscuro, 50%);
            }
        }
    }
}