@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    background-color: v.$grisClaro;
    height: 100%;

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal {
        flex: 1;
        @include m.tablet {
            overflow: scroll;
            overflow-x: hidden;

            &::-webkit-scrollbar {
                width: .9rem;
            }
        
            &::-webkit-scrollbar-track {
                background: v.$grisClaro;
            }

            &::-webkit-scrollbar-thumb {
                background-color: v.$indigo;
                border-radius: 1rem;
            }
        }
    }

    .contenido {
        padding: 2rem;

        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario {
        input:not([type="submit"]) {
            background-color: v.$blanco;
            border: 1px solid v.$gris;
        }

        input[type="submit"] {
            @include m.boton(v.$indigo, v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;
        }
    }

    .enlace {
        color: v.$indigo;
        background-color: v.$blanco;
        border: 1px solid v.$indigo;
        padding: 1rem 2rem;

        &:hover {
            color: darken($color: v.$indigo, $amount: 20%);
            border: 1px solid darken($color: v.$indigo, $amount: 20%);
        }
    }
}