@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal {
    position: absolute;
    background-color: rgb(0 0 0 /.8);
    height: 130vh;
    @include m.tablet {
        height: 100vh;
    }
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 2rem;
        @include m.tablet {
            margin: 0 auto;   
        }
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 1000ms;
        opacity: 0;
        border-radius: 1rem;

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        input[type="text"] {
            border: 1px solid v.$gris;
            border-radius: .5rem;
            background-color: v.$grisClaro;
        }

        .opciones {
            input {
                margin-bottom: 2rem;
            }
        }
        @include m.tablet {
            .opciones {
                display: flex;
                justify-content: space-between;
                
                input {
                    margin: 0;
                }
            }   
        }

        .submit-nueva-tarea {
            @include m.boton(v.$indigo, v.$blanco)
        }

        .cerrar-modal {
            @include m.boton(v.$naranja, v.$blanco)
        }

        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }

        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }
    }
}